import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
declare var $: any;

import { Options } from 'ng5-slider';
import { Filters } from '../../models/filter.model';
import { FilterProductsService } from '../../services/filter-products.service';
import { MobileService } from '../../services/mobile.service';
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  @Output('filterChange') filterChange: EventEmitter<Filters> = new EventEmitter<Filters>();
  @Input() type
  value: number = 100;
  minValue: number = 100;
  maxValue: number = 1000;
  options: Options = {
    floor: 0,
    ceil: 200
  };
  filterForm: FormGroup;
  @ViewChild('borderActive') borderActive;

  brands = []
  rams = []
  storages = []
  connectivities = []
  slots = []
  colorsArr = []
  AccessoryTypeArr = []
  MobileModelArr = []
  HardwarePartTypeArr = []

  checkedValue = false
  arr: any = []
  filter = new Filters()
  priceForm: FormGroup;
  constructor(
    private fb: FormBuilder,
    private _MobileService: MobileService,
    private _FilterSer: FilterProductsService
  ) { }


  ngOnInit(): void {
    this.showFilters()
  }

  defaultFilter() {
    this.createPriceForm();
    this.getBrands();
  }

  showFilters() {
    this.defaultFilter()
    switch (this.type) {
      case "MobilePhone":
        this.getRams();
        this.getSlots();
        this.getStorages();
        this.getConnectivity();
        this.getColours();
        break;
      case "Accessory":
        this.getAccessoryType();
        this.getMobileModel()
        break;
      case "HardwarePart":
        this.getMobileModel();
        this.getHardwarePartType()
        break;

      default:
        break;
    }

  }

  createPriceForm() {
    this.priceForm = this.fb.group({
      fromPrice: new FormControl(''),
      toPrice: new FormControl(''),
    });
  }

  getBrands() {
    this._FilterSer.getAllBrands().subscribe((response) => {
      this.brands = response.data
    })
  }

  getSlots() {
    this._FilterSer.getAllSlots().subscribe((response) => {
      this.slots = response.data
    })
  }

  getRams() {
    this._FilterSer.getAllRams().subscribe((response) => {
      this.rams = response.data
    })
  }

  getStorages() {
    this._FilterSer.getAllStorages().subscribe((response) => {
      this.storages = response.data
    })
  }

  getConnectivity() {
    this._FilterSer.getAllConnectivity().subscribe((response) => {
      this.connectivities = response.data
    })
  }

  getColours() {
    this._FilterSer.getAllColours().subscribe((response) => {
      this.colorsArr = response.data
    })
  }

  getAccessoryType() {
    this._FilterSer.getAllAccessoryType().subscribe((response) => {
      this.AccessoryTypeArr = response.data
    })
  }
  getMobileModel() {
    this._FilterSer.getAllMobileModel().subscribe((response) => {
      this.MobileModelArr = response.data
    })
  }
  getHardwarePartType() {
    this._FilterSer.getAllHardwarePartType().subscribe((response) => {
      this.HardwarePartTypeArr = response.data
    })
  }

  checkPriceValue() {
    let lowPrice = this.priceForm.controls.fromPrice.value;
    let highPrice = this.priceForm.controls.toPrice.value;
    let defaultLowPrice = 0;
    let defaultHighPrice = 99999;
    if (this.priceForm.valid) {
      if (lowPrice != '' && highPrice == '') {
        if (!this.filter.FilterRange.Price) {
          this.filter.FilterRange.Price = ''
        }
        this.filter.FilterRange.Price = `${lowPrice}-${defaultHighPrice}`
        this._MobileService.getAllProducts(this.type, this.filter).subscribe((response) => {
          console.log('iam here in sidebar', response.data);
          this._FilterSer.getData(response.data)
        })
      } else if (lowPrice == '' && highPrice != '') {
        if (!this.filter.FilterRange.Price) {
          this.filter.FilterRange.Price = ''
        }
        this.filter.FilterRange.Price = `${defaultLowPrice}-${highPrice}`
        this._MobileService.getAllProducts(this.type, this.filter).subscribe((response) => {
          console.log('iam here in sidebar', response.data);
          this._FilterSer.getData(response.data)
        })
      } else if (lowPrice != '' && highPrice != '') {
        if (!this.filter.FilterRange.Price) {
          this.filter.FilterRange.Price = ''
        }
        this.filter.FilterRange.Price = `${lowPrice}-${highPrice}`
        this.filterChange.emit(this.filter)
      } else {
        this.clearPriceValue()
      }
    }
  }

  clearPriceValue() {
    this.priceForm.controls.fromPrice.setValue('')
    this.priceForm.controls.toPrice.setValue('')
    this.filter.FilterRange.Price = ''
    this.filterChange.emit(this.filter)
  }


  checkBrandValue(e, brandData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileBrandId) {
        this.filter.FilterArray.MobileBrandId = []
      }
      this.filter.FilterArray.MobileBrandId.push(brandData.id)
    } else {
      this.filter.FilterArray.MobileBrandId = this.filter.FilterArray.MobileBrandId.filter((filterData: any) => {
        return brandData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }
  checkRamsValue(e, ramData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileRamId) {
        this.filter.FilterArray.MobileRamId = []
      }
      this.filter.FilterArray.MobileRamId.push(ramData.id)
    } else {
      // debugger;
      this.filter.FilterArray.MobileRamId = this.filter.FilterArray.MobileRamId.filter((filterData: any) => {
        return ramData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkSlotsValue(e, slotData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileSimCardId) {
        this.filter.FilterArray.MobileSimCardId = []
      }
      this.filter.FilterArray.MobileSimCardId.push(slotData.id)
    } else {
      // debugger;
      this.filter.FilterArray.MobileSimCardId = this.filter.FilterArray.MobileSimCardId.filter((filterData: any) => {
        return slotData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkConnectValue(e, connectData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileConnectivityId) {
        this.filter.FilterArray.MobileConnectivityId = []
      }
      this.filter.FilterArray.MobileConnectivityId.push(connectData.id)
    } else {
      this.filter.FilterArray.MobileConnectivityId = this.filter.FilterArray.MobileConnectivityId.filter((filterData: any) => {
        return connectData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkStorageValue(e, storageData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileStorageId) {
        this.filter.FilterArray.MobileStorageId = []
      }
      this.filter.FilterArray.MobileStorageId.push(storageData.id)
    } else {
      this.filter.FilterArray.MobileStorageId = this.filter.FilterArray.MobileStorageId.filter((filterData: any) => {
        return storageData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkColor(e, colorData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileColorId) {
        this.filter.FilterArray.MobileColorId = []
      }
      this.filter.FilterArray.MobileColorId.push(colorData.id)
    } else {
      this.filter.FilterArray.MobileColorId = this.filter.FilterArray.MobileColorId.filter((filterData: any) => {
        return colorData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkAccessoryTypeValue(e, accessoryData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.AccessoryTypeId) {
        this.filter.FilterArray.AccessoryTypeId = []
      }
      this.filter.FilterArray.AccessoryTypeId.push(accessoryData.id)
    } else {
      this.filter.FilterArray.AccessoryTypeId = this.filter.FilterArray.AccessoryTypeId.filter((filterData: any) => {
        return accessoryData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkMobileModelValue(e, mobileModelData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.MobileModelId) {
        this.filter.FilterArray.MobileModelId = []
      }
      this.filter.FilterArray.MobileModelId.push(mobileModelData.id)
    } else {
      this.filter.FilterArray.MobileModelId = this.filter.FilterArray.MobileModelId.filter((filterData: any) => {
        return mobileModelData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

  checkHardwarePartTypeValue(e, HardwarePartTypeData) {
    if (e.checked == true) {
      if (!this.filter.FilterArray.HardwarePartTypeId) {
        this.filter.FilterArray.HardwarePartTypeId = []
      }
      this.filter.FilterArray.HardwarePartTypeId.push(HardwarePartTypeData.id)
    } else {
      this.filter.FilterArray.HardwarePartTypeId = this.filter.FilterArray.HardwarePartTypeId.filter((filterData: any) => {
        return HardwarePartTypeData.id !== filterData
      })
    }
    this.filterChange.emit(this.filter)
  }

}
