import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { ProductCartService } from '../../services/ProductCart.service';
import { Type } from '../favourites/favourites.component'

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss'],
})
export class CheckoutComponent implements OnInit {
  ShippingFee: number = 18;
  @Output() changeTap = new EventEmitter();
  constructor(
    public ProductCartService: ProductCartService,
    private router: Router
  ) { }
  productArr = [];

  ngOnInit(): void {
    this.getAllCartItem()
  }
  deleteItem(item, index) {
    this.ProductCartService.deleteFromCart(item.id).subscribe((resp) => {
      this.getAllCartItem()
    })
  }

  getAllCartItem() {
    this.ProductCartService.getAllCartItem();
  }


  quantity(type, item) {
    if (type === "mins") {
      item.quantity = item.quantity > 1 ? item.quantity - 1 : 1;
      this.ProductCartService.orderOverViewData$value['orderItem'].map(items => {
        if (items.id === item.id) {
          items.quantity - 1
          //update quantity 
          let data = {
            id: items.id,
            quantity: items.quantity
          }
          this.ProductCartService.updateQuantity(data).subscribe(data => { })
        }
      })
    }
    else if (type === "plus") {
      item.quantity = item.quantity + 1
      this.ProductCartService.orderOverViewData$value['orderItem'].map(items => {
        if (items.id === item.id) {
          items.quantity + 1
          let data = {
            id: items.id,
            quantity: items.quantity
          }
          this.ProductCartService.updateQuantity(data).subscribe(data => { })
        }
      })
    }
    this.ProductCartService.getproductsTotalPrice();
  }


  goToDetails() {
    this.changeTap.emit('next');
    // this.router.navigate(['/products/address-details'])
  }

  goTo(item) {
    this.router.navigate([`products/${Type[item['categoryName']]}/${item['versionId']}`])
  }
}
