import { Component, OnInit } from '@angular/core';
import { retry } from 'rxjs/operators';
import { AlertService } from 'src/app/core/services';
import { CartRequest } from '../../models/cart-request.model';
import { FavoriteRequest } from '../../models/favorite-request.model';
import { Filters } from '../../models/filter.model';
import { FavouriteService } from '../../services/favourite.service';
import { FilterProductsService } from '../../services/filter-products.service';
import { MobileService } from '../../services/mobile.service';
import { ProductCartService } from '../../services/ProductCart.service';

@Component({
  selector: 'app-all-products',
  templateUrl: './all-products.component.html',
  styleUrls: ['./all-products.component.scss']
})
export class AllProductsComponent implements OnInit {
  pagDefValue = '1';
  showItemsDefValue = '16';
  highlightMobile = [];
  hotDealsMobile = [];
  highlightAccessory = [];
  hotDealsAccessory = [];
  highlightHardwarePart = [];
  hotDealsHardwarePart = [];
  filter = new Filters()
  constructor(
    private _MobileService: MobileService,
    private _FilterSer: FilterProductsService,
    private ProductCartService: ProductCartService,
    private FavouriteService: FavouriteService,
    private alertService: AlertService
  ) { }

  ngOnInit(): void {
    this.getHighLightMobile();
    this.getHotDealsMobile();
    this.getHighLightAccessory();
    this.getHotDealsAccessory();
    this.getHighLightHardwarePart();
    this.getHotDealsHardwarePart()
  }

  productType = ["Accessory", "MobilePhone", "HardwarePart"]

  getHighLightMobile() {
    this._MobileService.getHighlightProducts("MobilePhone")
      .subscribe((data) => {
        this.highlightMobile = data.data

      })
  }

  getHotDealsMobile() {
    this._MobileService.getHotDealProducts("MobilePhone").subscribe(data => {
      this.hotDealsMobile = data.data
    })

  }
  getHighLightAccessory() {
    this._MobileService.getHighlightProducts("Accessory")
      .subscribe((data) => {
        this.highlightAccessory = data.data

      })
  }

  getHotDealsAccessory() {
    this._MobileService.getHotDealProducts("Accessory").subscribe(data => {
      this.hotDealsAccessory = data.data
    })

  }
  getHighLightHardwarePart() {
    this._MobileService.getHighlightProducts("HardwarePart")
      .subscribe((data) => {
        this.highlightHardwarePart = data.data

      })
  }

  getHotDealsHardwarePart() {
    this._MobileService.getHotDealProducts("HardwarePart").subscribe(data => {
      this.hotDealsHardwarePart = data.data
    })

  }

  // {
  //   "userId": "string",
  //   "categoryId": 0,
  //   "versionId": 0
  // }
  addTo(type, category, product) {
    let productItemData: FavoriteRequest = {
      userId: localStorage.getItem('id'),
      categoryId: 1,
      versionId: 1
    }
    switch (category) {
      case "mobile":
        productItemData.categoryId = 1;
        productItemData.versionId = product['versionId']
        // this.addToFavourite(productItemData);
        break;
      case "accessory":
        productItemData.categoryId = 2;
        productItemData.versionId = product['versionId']
        // this.addToFavourite(productItemData);
        break;
      case "hardPart":
        productItemData.categoryId = 3;
        productItemData.versionId = product['versionId']
        // this.addToFavourite(productItemData);
        break;
    }
    if (type === "cart") {
      this.addToCart(productItemData)
    }
    else if (type === "favourite") {
      this.addToFavourite(productItemData);
    }
  }

  addToCart(product) {
    this.ProductCartService.addToCart(product).subscribe(data => {
      this.alertService.success('Added to Cart')
    })
  }

  addToFavourite(productItemData) {
    if (this.checkIfAlreadyFavourite(productItemData)) {
      let id = this.FavouriteService.favourites$value.filter(product => product.versionId === productItemData['versionId'] && product.categoryId === productItemData['categoryId'])[0].id
      this.FavouriteService.removeFromFavourites(id).subscribe(data => {
        this.alertService.warning('Deleted From Favorites')
      })

    } else {
      this.FavouriteService.addToFavourites(productItemData).subscribe(data => {
        this.alertService.success('Added to Favorites')
      })
    }

  }

  checkIfAlreadyFavourite(item) {
    return this.FavouriteService.favourites$value.some(product => product.versionId === item['versionId'] && product.categoryId === item['categoryId'])
  }
}
