import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/core/services';
import { UserData } from 'src/app/modules/auth/models/userData.model';
import { Filters } from './models/filter.model';
import { MobilePhoneUngrouped } from './models/mobile-phone.model';
import { Sort } from './models/sort.model';
import { MobileService } from './services/mobile.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
  sort: Sort
  mobiles: MobilePhoneUngrouped[] = []
  filters: Filters = new Filters()
  searchQuery: string
  userData: UserData;
  showItemsDefValue = '16';
  sortByDef = 'ID';
  sortTypeDef = 'ASC';
  @ViewChild('toggle') toggle
  status = false;
  type: string
  constructor(
    private authSrvc: AuthService,
    public mobileService: MobileService,
    private activeRoute: ActivatedRoute
  ) { }
  ngOnInit(): void {
    this.activeRoute.params.subscribe(data => {
      this.type = data['type']
    })
    this.getUserData();
    this.getProductData()
  }

  getProductData() {
    switch (this.type) {
      case "MobilePhone":
        this.getAllMobiles()
        break;
      case "Accessory":
        this.getAllAccessories()
        break;
      case "HardwarePart":
        this.getAllHardwarePart()
        break;
      default:
        break;
    }
  }
  addClass() {
    this.toggle.nativeElement.classList.toggle('open')
  }
  getUserData() {
    this.authSrvc.userData$.subscribe((data) => {
      if (data) this.userData = data
    })
  }
  searchProduct() {
    if (!this.searchQuery) return
    this.filters.FilterSearch = { global: this.searchQuery }
    this.getProductData()
  }
  resetSearch() {
    this.searchQuery = ""
    this.filters.FilterSearch = {}
  }
  filterChanges(filter: Filters) {
    if (filter.FilterArray && Object.keys(filter.FilterArray)) this.filters.FilterArray = { ...filter.FilterArray }
    if (filter.FilterValue && Object.keys(filter.FilterValue)) this.filters.FilterValue = { ...filter.FilterValue }
    if (filter.FilterRange && Object.keys(filter.FilterRange)) this.filters.FilterRange = { ...filter.FilterRange }
    this.resetSearch()
    this.getProductData()
  }
  getAllMobiles() {
    this.mobileService.getAllProducts("MobilePhone", this.filters, this.sort)
      .subscribe((res) => {
        this.mobiles = res.data
      })
  }

  getAllAccessories() {
    this.mobileService.getAllProducts("Accessory", this.filters, this.sort)
      .subscribe((res) => {
        this.mobiles = res.data
      })
  }
  getAllHardwarePart() {
    this.mobileService.getAllProducts("HardwarePart", this.filters, this.sort)
      .subscribe((res) => {
        this.mobiles = res.data
      })
  }

  getShowItems(showItems: number) {
    this.resetSearch()
    this.mobileService.setPagination(1, showItems)
    // this.getAllMobiles()
    this.getProductData()
  }
  getSortByItems(sortBy: number) {
    this.sort = new Sort()
    this.mobileService.setPagination(1)
    if (sortBy == 1) {
      this.sort.sortType = "ASC"
      this.sort.sortBy = "price"
    } else if (sortBy == 2) {
      this.sort.sortType = "DESC"
      this.sort.sortBy = "price"
    } else {
      this.sort = undefined
    }
    // this.getAllMobiles()
    this.getProductData()
  }
  getPagValue(i) {
    this.mobileService.setPagination(i)
    this.resetSearch()
    // this.getAllMobiles();
    this.getProductData()
  }

}
