import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment as env } from 'src/environments/environment';
import { Address } from '../components/checkout/order-over-view.model';

@Injectable({
  providedIn: 'root'
})
export class AddressService {

  constructor(private http: HttpClient) { }

  updateAddress(userData) {
    return this.http.post<ApiResponse>(`${env.baseUrl}Address`, userData);
  }

  getAddressById(addressId) {
    return this.http.get<ApiResponse<Address>>(`${env.baseUrl}Address/${addressId}`)
  }
}
