import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddressDetailsComponent } from './components/address-details/address-details.component';
import { AllProductsComponent } from './components/all-products/all-products.component';
import { CheckoutComponent } from './components/checkout/checkout.component';
import { DeliverAddressComponent } from './components/deliver-address/deliver-address.component';
import { FavouritesComponent } from './components/favourites/favourites.component';
import { OnlyProductViewComponent } from './components/only-product-view/only-product-view.component';
import { ThankYouOrderComponent } from './components/thank-you-order/thank-you-order.component';
import { ProductsComponent } from './products.component';



const routes: Routes = [
  { path: '', component: AllProductsComponent },
  // {
  //   path: 'accessories', children: [
  //     { path: ':id', component: OnlyProductViewComponent },
  //     { path: '', component: ProductsComponent },
  //   ]
  // },
  { path: 'check-out', component: CheckoutComponent },
  { path: 'deliverAddress', component: DeliverAddressComponent },
  { path: 'favourites', component: FavouritesComponent },
  { path: 'address-details', component: AddressDetailsComponent },
  { path: 'thank-you', component: ThankYouOrderComponent },
  {
    path: ':type', children: [
      { path: ':id', component: OnlyProductViewComponent },
      { path: '', component: ProductsComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductsRoutingModule { }


