import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Subject } from 'rxjs/internal/Subject';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment as env } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class FilterProductsService {
  filterObserval$: Observable<any>;
  private myFilterSubject = new Subject<any>();
  constructor(private http: HttpClient) {
    this.filterObserval$ = this.myFilterSubject.asObservable();
  }

  getData(data) {
    console.log('iam here in service', data); // I have data! Let's return it so subscribers can use it!
    // we can do stuff with data if we want
    this.myFilterSubject.next(data);
  }

  // Get All Brands
  getAllBrands() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileBrand?NoPaging=true`);
  }
  // Get All Rams
  getAllRams() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileRam?NoPaging=true`);
  }
  // Get All Slots
  getAllSlots() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileSimCard?NoPaging=true`);
  }
  // Get All Storages
  getAllStorages() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileStorage?NoPaging=true`);
  }
  // Get All Connectivity
  getAllConnectivity() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileConnectivity?NoPaging=true`);
  }

  // Get All Colours
  getAllColours() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileColor?NoPaging=true`);
  }

  // get getAccessoryType
  getAllAccessoryType() {
    return this.http.get<ApiResponse>(`${env.baseUrl}AccessoryType?NoPaging=true`);
  }

  // get getMobileModel
  getAllMobileModel() {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobileModel?NoPaging=true`);
  }

  getAllHardwarePartType() {
    return this.http.get<ApiResponse>(`${env.baseUrl}HardwarePartType?NoPaging=true`);
  }


}
