import { Injectable } from '@angular/core';
import { environment as env } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { ApiResponse } from 'src/app/models/api-response.model';
import { Filters } from '../models/filter.model';
import { Sort } from '../models/sort.model';
import { tap } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
import { PaginationMetaData } from '../models/pagintion-data.model';
import { MobilePhoneUngrouped } from '../models/mobile-phone.model';
@Injectable()
export class MobileService {
  paginationArr: { id: number }[] = []
  setPagination(pageNo?: number, pageSize?: number, count?: number) {
    if (!isNaN(pageNo)) this.pageNumber = pageNo
    if (!isNaN(pageSize)) this.pageSize = pageSize
    if (!isNaN(count)) this.count = count
  }
  pageSize: number = 10
  pageNumber: number = 1
  count: number
  constructor(private http: HttpClient) { }


  // All Mobiles Server Calling
  getMobileByIdServer(id) {
    return this.http.get<ApiResponse>(`${env.baseUrl}MobilePhone/byVersion-${id}`);
  }

  getHighlightProducts(type: string, pageNumber = 1, pageSize = 8) {
    return this.http.get<ApiResponse>(`${env.baseUrl}${type}/GetHighlight?pageSize=${pageSize}&pageNumber=${pageNumber}`)
  }
  getHotDealProducts(type: string, pageNumber = 1, pageSize = 4) {
    return this.http.get<ApiResponse>(`${env.baseUrl}${type}/GetHotDeal?pageSize=${pageSize}&pageNumber=${pageNumber}`)
  }


  getAllProducts(type: string, filter?: Filters, sort?: Sort) {
    let searchParams = '';
    searchParams = `?pageSize=${this.pageSize}&PageNumber=${this.pageNumber}`;
    if (sort) {
      searchParams += `&SortBy=${sort.sortBy}&SortType=${sort.sortType}`
    }
    if (filter) {
      let filterKeys = Object.keys(filter)
      filterKeys.forEach(key => {
        if (key == "FilterRange") {
          let filterKeysOfValues = Object.keys(filter[key])
          filterKeysOfValues.forEach(_key => {
            searchParams += `&${key}[${_key}]=${filter[key][_key]}`
          })
        } else if (key == "FilterArray") {
          let filterKeysOfValues = Object.keys(filter[key])
          filterKeysOfValues.forEach(_key => {
            if (filter[key][_key].length) {
              searchParams += `&${key}[${_key}]=${filter[key][_key]}`
            }
          });
        } else if (key == "FilterSearch") {
          let filterKeysOfValues = Object.keys(filter[key])
          filterKeysOfValues.forEach(_key => {
            searchParams += `&${key}[${_key}]=${filter[key][_key]}`
          });
        } else if (key == "FilterValue") {
          let filterKeysOfValues = Object.keys(filter[key])
          filterKeysOfValues.forEach(_key => {
            searchParams += `&${key}[${_key}]=${filter[key][_key]}`
          });
        }
      })
    }

    if (type === "HardwarePart") {
      return this.http.get<ApiResponse<MobilePhoneUngrouped[]>>(`${env.baseUrl}${type}${searchParams}`)
        .pipe(tap((res) => {
          let count = res['count'];
          this.setPagination(undefined, undefined, count)
        }
        ))
    }
    return this.http.get<ApiResponse<MobilePhoneUngrouped[]>>(`${env.baseUrl}${type}/GetVersioned${searchParams}`)
      .pipe(tap((res) => {
        let count = res['count'];
        this.setPagination(undefined, undefined, count)
      }
      ))
  }
  get pagesArr(): number[] {
    if (!isNaN(this.count)) {
      let pagesCount = Math.ceil(this.count / Number(this.pageSize))
      let paginationArr = Array(pagesCount).fill(0).map((x, i) => i + 1);
      if (this.count == 0) paginationArr = []
      return paginationArr
    } else {
      return []
    }
  }

}
