import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ProductCartService } from '../../services/ProductCart.service';

@Component({
  selector: 'app-payment-information',
  templateUrl: './payment-information.component.html',
  styleUrls: ['./payment-information.component.scss']
})
export class PaymentInformationComponent implements OnInit {
  @Output() changeTap = new EventEmitter();

  constructor(private ProductCartService: ProductCartService, private fb: FormBuilder,) { }
  pymentMethodForm: FormGroup;
  ngOnInit(): void {
    this.createPymentMethodForm();
  }


  createPymentMethodForm() {
    this.pymentMethodForm = this.fb.group({
      PAYMENT: new FormControl({ value: '', disabled: true }, []),
      CARDNOM: new FormControl({ value: '', disabled: true }, []),
      CODE: new FormControl({ value: '', disabled: true }, []),
      EXPIRED_DATE: new FormControl({ value: '', disabled: true }, []),
    });
  }

  orderView() {
    // if (!this.pymentMethodForm.valid) {
    //   return this.pymentMethodForm.markAllAsTouched();
    // }
    this.ProductCartService.orderOverViewData$.next(Object.assign(this.ProductCartService.orderOverViewData$value, { pymentMethod: this.pymentMethodForm.value }))
    this.changeTap.emit('next');
  }

}
